@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,500&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	--default-font: 'Roboto', sans-serif;
	--orange: #febd69;
	--dark: #131921;
}

body {
	background-color: #e6e6e6;
	font-family: var(--default-font);
}

.link-tag {
	color: white;
	text-decoration: none;
}

.cart-page {
	position: relative;
	width: 100%;
}

.all-cart-products-cont {
	transition: all 0.5s ease-in-out;
	overflow: hidden;
}

.card-title-cont {
	margin: 1rem 0;
	margin-left: 40px;
	width: 65%;
	padding: 0.8rem 1.5rem;
	background-color: #fff;
	background-color: #fff;
}

.cart-title {
	font-size: 1.8rem;
	font-weight: 600;
}

.cart-prod-container {
	display: flex;
	margin: 1rem 0;
	overflow: hidden;
	margin-left: 40px;
	height: 14rem;
	padding: 1rem;
	width: 65%;
	background-color: #fff;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
	transition: 0.5s all ease-in-out;
}

.cart-img-cont {
	display: flex;
	justify-content: center;
	width: 200px;
	overflow: hidden;
}

.cart-prod-details-cont {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	width: 70%;
}

.cart-prod-img {
	max-height: 180px;
	max-width: 160px;
	object-fit: contain;
}
.cart-prod-title {
	font-size: 1.3rem;
	font-weight: 600;
}

.cart-prod-price {
	font-size: 1.5rem;
	font-weight: 500;
	color: rgb(131, 0, 0);
}

.subtotal-cont {
	position: absolute;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-evenly;
	right: 0;
	top: 0;
	padding: 1rem;
	margin-right: 30px;
	width: 29%;
	height: 100px;
	border: 1px solid grey;
	border-radius: 10px;
	background-color: gainsboro;
}

.del-prod-cont {
	margin: 10px 0;
}

.checkout-btn {
	background: linear-gradient(to top, #efbf44, rgb(244, 210, 125));
	font-size: 1rem;
	color: var(--dark);
	border: none;
	border-radius: 5px;
	outline: none;
	cursor: pointer;
	font-weight: 500;
	padding: 5px 1rem;
	width: 90%;
	margin: 0 auto;
}

.checkout-btn-link {
	width: 80%;
}

.del-prod-btn {
	background: linear-gradient(to top, #efbf44, rgb(244, 210, 125));
	font-size: 1rem;
	color: var(--dark);
	border: none;
	border-radius: 5px;
	outline: none;
	cursor: pointer;
	font-weight: 500;
	padding: 5px 5px;
	height: 30px;
	width: 160px;
	margin-bottom: 30px;
}

.checkout-btn:hover,
.login-btn:hover,
.del-prod-btn:hover,
.place-o-btn:hover {
	background: linear-gradient(to top, #e08703, rgb(255, 217, 120));
}

.login-page {
	width: 100%;
	height: 100vh;
	overflow: hidden;
	display: flex;
	justify-content: center;
	background-color: #fff;
}

.login-form-cont {
	margin-top: -3rem;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.login-form {
	margin-top: 1.4rem;
	width: 27rem;
	border-radius: 5px;
	border: 1px solid rgba(0, 0, 0, 0.3);
	padding: 2rem;
	background-color: #fff;
	display: flex;
	flex-direction: column;
}

.login-logo {
	width: 180px;
}

.login-label-top {
	font-size: 2rem;
}

.login-label {
	margin-top: 15px;
}

.login-input {
	padding-left: 5px;
	font-size: 1.2rem;
	margin-top: 10px;
	height: 35px;
	border: 1px solid grey;
	border-radius: 5px;
	outline: none;
}

.login-btn,
.signup-btn {
	background: linear-gradient(to top, #efbf44, rgb(244, 210, 125));
	font-size: 1rem;
	color: var(--dark);
	border: 1px solid grey;
	border-radius: 5px;
	outline: none;
	cursor: pointer;
	font-weight: 500;
	padding: 5px 5px;
	height: 35px;
	margin-top: 1.5rem;
	margin-bottom: 1.3rem;
}

.dummy-text {
	font-size: 0.81rem;
}

.signup-btn {
	width: 100%;
	background: gainsboro;
}

.signup-btn:hover {
	background: linear-gradient(to top, gainsboro, white);
}

.display-leave {
	max-height: 14rem;
	opacity: 1;
}
.display-leave-active {
	max-height: 0;
	opacity: 0;
	transform: translateX(-20rem);
	transition: all 200ms ease-in;
}

.product-overflow-container {
	overflow: hidden;
}

.noti-cont {
	padding: 10px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.noti-img {
	max-height: 100px;
	max-width: 90px;
	object-fit: contain;
}

.noti-msg {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	margin: 0 30px;
	color: black;
}

.hidden-div {
	display: none;
}

.checkout-page {
	position: relative;
	width: 100%;
	height: 90vh;
}

.shipping-add-cont {
	display: flex;
}

.checkout-page-content {
	width: 65%;
	border-radius: 5px;
	padding: 0 2.6rem;
	padding-top: 1.2rem;
	font-family: var(--default-font);
}

.shipping-add-cont {
	overflow: hidden;
	min-height: 8rem;
	padding: 1rem 0;
}

.checkout-label {
	min-width: 20%;
	font-weight: 600;
	margin-bottom: 10px;
}

.checkout-textarea {
	border: 1px solid gainsboro;
	border-radius: 5px;
	padding-top: 5px;
	padding-left: 5px;
	height: 6rem;
	resize: none;
	width: 80%;
	outline: black;
	font-family: var(--default-font);
	font-size: 1rem;
	transition: 0.2s all ease-in-out;
}

.checkout-textarea:focus {
	border: 1px solid rgba(0, 0, 0, 0.28);
}

.single-pay-opt {
	margin-bottom: 5px;
}

.single-pay-opt > label {
	margin-left: 13px;
}

.optional-container {
	padding: 0.7rem 0;
}

.place-cont {
	position: absolute;
	display: flex;
	flex-direction: column;
	align-items: start;
	justify-content: space-evenly;
	right: 0;
	top: 0;
	margin-top: 1rem;
	margin-right: 30px;
	padding: 1.5rem;
	width: 29%;
	border: 1px solid grey;
	border-radius: 10px;
	background-color: gainsboro;
}

.place-o-show-details {
	display: flex;
	flex-direction: column;
}

.place-o-btn {
	position: relative;
	background: linear-gradient(to top, #efbf44, rgb(244, 210, 125));
	font-size: 1rem;
	color: var(--dark);
	border: none;
	border-radius: 5px;
	outline: none;
	cursor: pointer;
	font-weight: 500;
	padding: 10px 1rem;
	width: 100%;
	margin: 0 auto;
}

.place-o-show-details {
	width: 100%;
	margin-top: 10px;
}
.place-o-show-details > hr {
	margin: 15px 0;
}

.order-details {
	margin: 5px 0;
	display: flex;
	justify-content: space-between;
}

.order-total {
	font-size: 1.3rem;
	font-weight: 600;
	color: rgb(179, 0, 0);
}

@media screen and (max-width: 600px) {
	.login-form {
		width: 20rem;
		border-radius: 5px;
		border: 1px solid rgba(0, 0, 0, 0.3);
		padding: 1.3rem;
		display: flex;
		flex-direction: column;
	}

	.login-logo {
		width: 140px;
	}

	.cart-page {
		padding-top: 110px;
	}
	.card-title-cont {
		margin: 0.2rem auto;
		width: 90%;
		padding: 0.8rem 1.5rem;
		background-color: #fff;
		background-color: #fff;
	}

	.cart-title {
		font-size: 1.8rem;
		font-weight: 600;
	}

	.cart-prod-container {
		margin: 1rem auto;
		height: 10rem;
		width: 90%;
	}
	.cart-prod-details-cont {
		width: 80%;
	}

	.cart-prod-img {
		max-height: 120px;
		max-width: 90px;
		object-fit: contain;
	}
	.cart-prod-title {
		font-size: 0.9rem;
	}

	.cart-prod-price {
		font-size: 1rem;
	}
	.del-prod-cont {
		font-size: 0.7rem;
	}
	.subtotal-cont {
		position: relative;
		margin: 10px auto;
		width: 90%;
	}
	.checkout-page {
		padding-top: 110px;
	}
	.checkout-page-content {
		width: 100%;
		padding: 0 1rem;
	}
	.shipping-add-cont {
		flex-direction: column;
		padding: 5px 0;
	}
	.checkout-textarea {
		width: 100%;
	}
	.place-cont {
		position: relative;
		margin-right: 0px;
		padding: 1.2rem;
		width: 92%;
		margin: 10px auto;
		margin-bottom: 20px;
	}

	.place-o-show-details {
		display: flex;
		flex-direction: column;
	}

	.place-o-btn {
		position: relative;
		background: linear-gradient(to top, #efbf44, rgb(244, 210, 125));
		font-size: 1rem;
		color: var(--dark);
		border: none;
		border-radius: 5px;
		outline: none;
		cursor: pointer;
		font-weight: 500;
		padding: 10px 1rem;
		width: 100%;
		margin: 10px auto;
	}

	.place-o-show-details {
		width: 100%;
		margin-top: 0px;
	}
	.place-o-show-details > hr {
		margin: 15px 0;
	}

	.order-details {
		margin: 5px 0;
		display: flex;
		justify-content: space-between;
	}

	.order-total {
		font-size: 1.3rem;
		font-weight: 600;
		color: rgb(179, 0, 0);
	}
}

nav {
	display: flex;
	color: white;
	width: 100%;
	height: 60px;
	background-color: var(--dark);
	top: 0;
	z-index: 100;
}

.nav-items {
	position: relative;
	width: 100%;
	display: flex;
}

li {
	list-style: none;
}

.logo {
	cursor: pointer;
	margin-left: 40px;
	margin-right: 20px;
	margin-top: 7px;
	width: 7rem;
}

.search-bar-container {
	position: relative;
}

.search-bar {
	font-family: var(--default-font);
	position: relative;
	color: black;
	outline: none;
	font-size: 1rem;
	padding-left: 10px;
	border: none;
	width: 54vw;
	height: 2.3rem;
	margin: 11px 10px;
	border-radius: 5px;
}

.search-btn-container {
	display: flex;
	align-items: center;
	position: absolute;
	top: 11px;
	height: 2.3rem;
	right: 0.5rem;
	padding: 0 8px;
	background-color: var(--orange);
	border-top-right-radius: 5px;
	border-bottom-right-radius: 5px;
}

.search-btn {
	cursor: pointer;
	font-size: 1.7rem;
	color: var(--dark);
}

.right-links {
	display: flex;
	justify-content: space-evenly;
}

.right-text {
	cursor: pointer;
	font-family: var(--default-font);
	display: flex;
	flex-direction: column;
	max-height: 50px;
	overflow: hidden;
	margin: 10px 10px;
}

.cart-cont {
	cursor: pointer;
	display: flex;
	align-items: center;
	margin: 7px 10px;
}

.cart-logo {
	font-size: 2.1rem;
	color: white;
	margin: 0 5px;
}

.cart-num {
	font-family: var(--default-font);
	font-size: 1.4rem;
	font-weight: 700;
	color: var(--orange);
}

.nav-upper-text {
	font-size: 0.8rem;
	font-weight: 300;
}

.active {
	color: #febd69;
}

.sign-out-cont {
	margin: 3.1rem -20px;
	position: absolute;
	background-color: #fff;
	z-index: 300;
	max-height: 0;
	overflow: hidden;
	transition: 0.1s all ease-in;
}

.sign-arrow {
	opacity: 0;
	position: absolute;
	z-index: 300;
	font-size: 2rem;
	transform: rotate(-90deg);
	color: white;
	bottom: -19px;
	transition: 0.1s all ease-in;
}

.nav-name-field:hover .sign-arrow {
	opacity: 1;
}

.nav-name-field:hover .sign-out-cont {
	max-height: 3.5rem;
}

.sign-out-btn {
	position: relative;
	margin: 0.8rem;
	background: linear-gradient(to top, #efbf44, rgb(244, 210, 125));
	color: var(--dark);
	border: none;
	border-radius: 5px;
	outline: none;
	cursor: pointer;
	padding: 8px;
	font-weight: 700;
}

.menu-icon {
	display: none;
}

.sidebar-cont {
	left: -1000rem;
	position: absolute;
	z-index: 200000000000;
	margin-top: 40px;
	background-color: var(--dark);
	width: 100vw;
	height: 1000px;
	transition: 0.2s all linear;
}

/* FOR MOBILE */

@media screen and (max-width: 1100px) {
	nav {
		flex-direction: column;
		height: 100px;
		position: fixed;
		top: 0;
	}
	.top-links {
		display: flex;
		justify-content: space-between;
	}
	.mobile-left-links {
		display: flex;
	}
	.mobile-right-links {
		display: flex;
		margin: 0;
	}

	.logo {
		width: 80px;
		margin-top: 13px;
		margin-left: 10px;
	}
	.menu-icon {
		margin-top: 13px;
		margin-left: 10px;
		display: block;
		font-size: 1.3rem;
	}
	.search-bar-container {
		padding: 0 10px;
	}
	.search-bar {
		width: 98vw;
		height: 2rem;
		margin: 5px 0;
		margin-left: 4px;
	}

	.search-btn-container {
		top: 5px;
		height: 2rem;
		right: 10px;
		background-color: var(--orange);
	}
	.right-text {
		max-height: 50px;
		font-size: 1rem;
		margin: 15px 6px;
	}

	.cart-cont {
		margin-top: 2px;
		margin-right: 10px;
	}

	.cart-logo {
		font-size: 1.9rem;
		color: white;
		margin-top: 10px;
	}

	.cart-num {
		font-family: var(--default-font);
		font-size: 1.4rem;
		font-weight: 700;
		color: var(--orange);
	}

	.sidebar-links {
		margin-top: 2rem;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.nav-mobile-text {
		font-size: 1.3rem;
	}
}

@media screen and (max-width: 800px) {
	.search-bar {
		width: 95vw;
	}
}

@media screen and (max-width: 600px) {
	.search-bar {
		width: 93vw;
	}
}

.header-img-container {
	position: relative;
}

.header-img {
	height: 80vh;
	width: 100%;
	background: url(/react-amazon-clone/static/media/header.60026965.jpg) no-repeat center center;
	background-size: cover;
}

.header-img::after {
	position: absolute;
	content: '';
	bottom: 0;
	width: 100%;
	height: 50vh;
	background: linear-gradient(to top, #e6e6e6, transparent);
	opacity: 1;
}

@media only screen and (max-width: 1100px) {
	.header-img-container {
		position: relative;
		display: flex;
		justify-content: center;
	}
	.header-img {
		width: 100%;
		background: url("/react-amazon-clone/static/media/mobile header.8c04d548.jpg") no-repeat center top;
		background-size: cover;
	}
}

@media screen and (max-width: 720px) {
	.header-img-container {
		position: relative;
		display: flex;
		justify-content: center;
	}
	.header-img {
		margin-top: 100px;
		width: 100%;
		height: 350px;
		background: url("/react-amazon-clone/static/media/mobile header.8c04d548.jpg") no-repeat center top;
		background-size: cover;
	}

	.header-img::after {
		display: none;
	}
}

@media screen and (max-width: 600px) {
	.header-img-container {
		position: relative;
		display: flex;
		justify-content: center;
	}
	.header-img {
		margin-top: 100px;
		width: 600px;
		height: 250px;
		margin-top: 100px;
		background: url("/react-amazon-clone/static/media/mobile header.8c04d548.jpg") no-repeat center top;
		background-size: cover;
	}

	.header-img::after {
		display: none;
	}
}

.display-all-products {
	position: absolute;
	top: 400px;
	padding-bottom: 20px;
}

.product-row {
	display: flex;
	justify-content: space-evenly;
}

.product-container {
	cursor: pointer;
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	width: 30%;
	border-radius: 10px;
	background-color: #fff;
	margin: 10px 0;
	padding: 1rem;
	box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
	transition: 0.2s all ease-in-out;
}
.product-container:hover {
	transform: scale(1.03);
	box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
}

.details {
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	height: 100%;
}

.product-title {
	font-size: 1.3rem;
	font-weight: 600;
}

.product-price {
	font-size: 1.5rem;
	font-weight: 500;
	color: rgb(131, 0, 0);
}

.product-img {
	width: 100%;
	max-height: 200px;
	object-fit: contain;
	margin: 20px auto;
}

.stars {
	display: flex;
}

.add-btn {
	background: linear-gradient(to top, #efbf44, rgb(244, 210, 125));
	font-size: 1.2rem;
	color: var(--dark);
	border: none;
	border-radius: 5px;
	outline: none;
	cursor: pointer;
	font-weight: 500;
	padding: 5px 5px;
	width: 13rem;
	margin: 0 auto;
	transition: 0.3s all ease-in-out;
}

.add-btn:hover {
	background: linear-gradient(to top, #e08703, rgb(255, 217, 120));
}

@media only screen and (max-width: 720px) {
	.display-all-products {
		position: relative;
		top: 10px;
	}

	.product-row {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	.product-container {
		cursor: pointer;
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		width: 90%;
		border-radius: 10px;
		background-color: #fff;
		margin: 10px 0;
		padding: 1rem;
		box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
		transition: 0.2s all ease-in-out;
	}
}

